/**
 * OCR ê²°ê³¼ í…ìŠ¤íŠ¸ë¥¼ íŒŒì‹±í•˜ì—¬ ê·¼íƒœ ê¸°ë¡ìœ¼ë¡œ ë³€í™˜í•˜ëŠ” ìœ í‹¸ë¦¬í‹°
 */

// í•œê¸€ ì‹œê°„ í˜•ì‹ì„ HH:MMìœ¼ë¡œ ë³€í™˜ (ì˜ˆ: "8ì‹œ 12ë¶„" â†’ "08:12")
export const parseKoreanTime = (text) => {
  if (!text) return null;

  // "8ì‹œ 12ë¶„" ë˜ëŠ” "08ì‹œ 12ë¶„" í˜•ì‹
  const koreanMatch = text.match(/(\d{1,2})ì‹œ\s*(\d{1,2})ë¶„/);
  if (koreanMatch) {
    const hours = koreanMatch[1].padStart(2, '0');
    const minutes = koreanMatch[2].padStart(2, '0');
    return `${hours}:${minutes}`;
  }

  // "08:12" ë˜ëŠ” "8:12" í˜•ì‹
  const colonMatch = text.match(/(\d{1,2}):(\d{2})/);
  if (colonMatch) {
    const hours = colonMatch[1].padStart(2, '0');
    const minutes = colonMatch[2];
    return `${hours}:${minutes}`;
  }

  return null;
};

// í•œê¸€ ì‹œê°„ì„ ë¶„ìœ¼ë¡œ ë³€í™˜ (ì˜ˆ: "0ì‹œ 50ë¶„" â†’ 50ë¶„, "1ì‹œ 30ë¶„" â†’ 90ë¶„)
export const parseKoreanTimeToMinutes = (text) => {
  if (!text) return 0;

  const koreanMatch = text.match(/(\d{1,2})ì‹œ\s*(\d{1,2})ë¶„/);
  if (koreanMatch) {
    const hours = parseInt(koreanMatch[1], 10);
    const minutes = parseInt(koreanMatch[2], 10);
    return hours * 60 + minutes;
  }

  // "1:30:00" í˜•ì‹ (ì ì‹¬ì‹œê°„)
  const hhmmssMatch = text.match(/(\d{1,2}):(\d{2}):(\d{2})/);
  if (hhmmssMatch) {
    const hours = parseInt(hhmmssMatch[1], 10);
    const minutes = parseInt(hhmmssMatch[2], 10);
    return hours * 60 + minutes;
  }

  return 0;
};

// í•œê¸€ ë‚ ì§œ íŒŒì‹± (ì˜ˆ: "12ì›” 1ì¼" â†’ { month: 12, day: 1 })
export const parseKoreanDate = (text) => {
  if (!text) return null;

  // "12ì›” 1ì¼" ë˜ëŠ” "12ì›” 01ì¼" í˜•ì‹
  const match = text.match(/(\d{1,2})ì›”\s*(\d{1,2})ì¼/);
  if (match) {
    return {
      month: parseInt(match[1], 10),
      day: parseInt(match[2], 10)
    };
  }

  return null;
};

// ì—°ë„ ì¶”ë¡  (í˜„ì¬ ì›” ê¸°ì¤€)
export const inferYear = (month, currentMonth) => {
  const [currentYear, currentMon] = currentMonth.split('-').map(Number);

  // 12ì›” ë°ì´í„°ì¸ë° í˜„ì¬ê°€ 1ì›”ì´ë©´ â†’ ì´ì „ ì—°ë„
  if (month === 12 && currentMon <= 2) {
    return currentYear - 1;
  }

  // 1ì›” ë°ì´í„°ì¸ë° í˜„ì¬ê°€ 12ì›”ì´ë©´ â†’ ë‹¤ìŒ ì—°ë„
  if (month === 1 && currentMon >= 11) {
    return currentYear + 1;
  }

  return currentYear;
};

// ISO ë‚ ì§œ ë¬¸ìì—´ ìƒì„±
export const toISODateString = (year, month, day) => {
  return `${year}-${String(month).padStart(2, '0')}-${String(day).padStart(2, '0')}`;
};

// ë©”ëª¨ í‚¤ì›Œë“œ ê°ì§€
const MEMO_KEYWORDS = [
  'ì—°ì°¨', 'ë°˜ì°¨', 'ì˜¤ì „ë°˜ì°¨', 'ì˜¤í›„ë°˜ì°¨', 'íœ´ê°€', 'ë³‘ê°€',
  'ë§¤ë‰´ì–¼', 'í•˜ë‚¨', 'ì˜¤í”ˆ', 'ì—°ì¶œ', 'í¬ë¦¬ìŠ¤ë§ˆìŠ¤', 'ê³µí†µíœ´ë¬´',
  'êµìœ¡', 'ì¶œì¥', 'ì¬íƒ', 'íšŒì˜'
];

export const detectMemoKeyword = (text) => {
  if (!text) return '';

  for (const keyword of MEMO_KEYWORDS) {
    if (text.includes(keyword)) {
      return keyword;
    }
  }

  return text.trim();
};

// OCR í…ìŠ¤íŠ¸ í•œ ì¤„ì„ íŒŒì‹±í•˜ì—¬ ë ˆì½”ë“œë¡œ ë³€í™˜
export const parseOCRLine = (line) => {
  // ë‚ ì§œ ì¶”ì¶œ
  const dateInfo = parseKoreanDate(line);
  if (!dateInfo) return null;

  // ì‹œê°„ íŒ¨í„´ ì°¾ê¸° - í•œê¸€ ì‹œê°„ í˜•ì‹ ëª¨ë‘ ì¶”ì¶œ
  const timeMatches = line.match(/(\d{1,2})ì‹œ\s*(\d{1,2})ë¶„/g) || [];

  let startTime = null;
  let endTime = null;
  let excludeTime = 0;

  // ì‹œê°„ íŒ¨í„´ ë¶„ì„ (ë³´í†µ ì¶œê·¼, í‡´ê·¼, ì œì™¸ ìˆœì„œ)
  if (timeMatches.length >= 2) {
    startTime = parseKoreanTime(timeMatches[0]);
    endTime = parseKoreanTime(timeMatches[1]);

    // ì œì™¸ì‹œê°„ì´ ìˆëŠ” ê²½ìš° (ì„¸ ë²ˆì§¸ ì‹œê°„ íŒ¨í„´)
    if (timeMatches.length >= 3) {
      excludeTime = parseKoreanTimeToMinutes(timeMatches[2]);
    }
  }

  // ë©”ëª¨ ì¶”ì¶œ
  const memo = detectMemoKeyword(line);

  return {
    dateInfo,
    startTime,
    endTime,
    excludeTime,
    memo,
    rawText: line,
    confidence: calculateConfidence(startTime, endTime)
  };
};

// ì‹ ë¢°ë„ ê³„ì‚°
const calculateConfidence = (startTime, endTime) => {
  if (!startTime && !endTime) return 'low';
  if (startTime && endTime) return 'high';
  return 'medium';
};

// OCR ì „ì²´ í…ìŠ¤íŠ¸ë¥¼ íŒŒì‹±í•˜ì—¬ ë ˆì½”ë“œ ë°°ì—´ë¡œ ë³€í™˜
export const parseOCRText = (ocrText, currentMonth) => {
  if (!ocrText) return [];

  const lines = ocrText.split('\n').filter(line => line.trim());
  const records = [];

  for (const line of lines) {
    const parsed = parseOCRLine(line);

    if (parsed?.dateInfo) {
      const year = inferYear(parsed.dateInfo.month, currentMonth);
      const dateStr = toISODateString(year, parsed.dateInfo.month, parsed.dateInfo.day);

      records.push({
        date: dateStr,
        startTime: parsed.startTime,
        endTime: parsed.endTime,
        lunchTime: 90, // ê¸°ë³¸ ì ì‹¬ì‹œê°„
        excludeTime: parsed.excludeTime,
        memo: parsed.memo,
        confidence: parsed.confidence,
        rawText: parsed.rawText
      });
    }
  }

  return records;
};

// ì—‘ì…€ì—ì„œ ë³µì‚¬í•œ íƒ­ êµ¬ë¶„ í…ìŠ¤íŠ¸ íŒŒì‹±
export const parseExcelPastedText = (pastedText, currentMonth) => {
  if (!pastedText) return [];

  const lines = pastedText.split('\n').filter(line => line.trim());
  const records = [];

  for (const line of lines) {
    // íƒ­ìœ¼ë¡œ ë¶„ë¦¬
    const columns = line.split('\t');
    if (columns.length < 4) continue;

    // ì²« ë²ˆì§¸ ì—´: ë‚ ì§œ (12ì›” 1ì¼)
    const dateInfo = parseKoreanDate(columns[0]);
    if (!dateInfo) continue;

    // ì¶œê·¼ì‹œê°„ (ë³´í†µ 3~4ë²ˆì§¸ ì—´)
    let startTime = null;
    let endTime = null;
    let excludeTime = 0;
    let memo = '';

    // ê° ì—´ì—ì„œ ì‹œê°„ íŒ¨í„´ ì°¾ê¸°
    for (let i = 1; i < columns.length; i++) {
      const col = columns[i].trim();

      // ì‹œê°„ íŒ¨í„´ í™•ì¸
      const timeValue = parseKoreanTime(col);
      if (timeValue) {
        const hours = parseInt(timeValue.split(':')[0], 10);
        // ì¶œê·¼ì‹œê°„: 6ì‹œ~12ì‹œ ì‚¬ì´
        if (!startTime && hours >= 6 && hours <= 12) {
          startTime = timeValue;
        }
        // í‡´ê·¼ì‹œê°„: 13ì‹œ~24ì‹œ ì‚¬ì´
        else if (!endTime && hours >= 13 && hours <= 24) {
          endTime = timeValue;
        }
        // ì œì™¸ì‹œê°„: 0~3ì‹œ ë²”ìœ„
        else if (hours <= 3) {
          excludeTime = parseKoreanTimeToMinutes(col);
        }
      }

      // ë©”ëª¨ í‚¤ì›Œë“œ í™•ì¸
      const keyword = detectMemoKeyword(col);
      if (keyword && keyword !== col.trim()) {
        memo = keyword;
      } else if (col.length > 0 && col.length < 20 && !timeValue && !col.match(/^\d+[WwWwì£¼]?$/) && !col.match(/^[ì›”í™”ìˆ˜ëª©ê¸ˆí† ì¼]$/)) {
        // ì£¼ì°¨ë‚˜ ìš”ì¼ì´ ì•„ë‹Œ í…ìŠ¤íŠ¸ëŠ” ë©”ëª¨ë¡œ ê°„ì£¼
        if (!memo && col.length > 1) {
          memo = col;
        }
      }
    }

    const year = inferYear(dateInfo.month, currentMonth);
    const dateStr = toISODateString(year, dateInfo.month, dateInfo.day);

    records.push({
      date: dateStr,
      startTime,
      endTime,
      lunchTime: 90,
      excludeTime,
      memo,
      confidence: startTime && endTime ? 'high' : startTime || endTime ? 'medium' : 'low',
      rawText: line
    });
  }

  return records;
};

// í…Œì´ë¸” í˜•ì‹ OCR í…ìŠ¤íŠ¸ íŒŒì‹± (í–‰ ê¸°ë°˜)
export const parseTableOCRText = (ocrText, currentMonth) => {
  if (!ocrText) return [];

  const lines = ocrText.split('\n').filter(line => line.trim());
  const records = [];

  for (const line of lines) {
    // ë‚ ì§œê°€ í¬í•¨ëœ í–‰ì¸ì§€ í™•ì¸
    const dateInfo = parseKoreanDate(line);
    if (!dateInfo) continue;

    // ì‹œê°„ íŒ¨í„´ë“¤ ì¶”ì¶œ
    const allTimes = [];
    const koreanTimeRegex = /(\d{1,2})ì‹œ\s*(\d{1,2})ë¶„/g;
    let match;

    while ((match = koreanTimeRegex.exec(line)) !== null) {
      allTimes.push({
        text: match[0],
        hours: parseInt(match[1], 10),
        minutes: parseInt(match[2], 10),
        index: match.index
      });
    }

    // ì‹œê°„ ë¶„ë¥˜: ì²« ë²ˆì§¸ëŠ” ì¶œê·¼, ë‘ ë²ˆì§¸ëŠ” í‡´ê·¼, ë‚˜ë¨¸ì§€ëŠ” ì œì™¸ì‹œê°„
    let startTime = null;
    let endTime = null;
    let excludeTime = 0;

    if (allTimes.length >= 1) {
      // ì²« ë²ˆì§¸ ì‹œê°„ì´ 0~12ì‹œ ì‚¬ì´ë©´ ì¶œê·¼ì‹œê°„ìœ¼ë¡œ íŒë‹¨
      const firstTime = allTimes[0];
      if (firstTime.hours >= 0 && firstTime.hours <= 12) {
        startTime = parseKoreanTime(firstTime.text);
      }
    }

    if (allTimes.length >= 2) {
      // ë‘ ë²ˆì§¸ ì‹œê°„ì´ 12~24ì‹œ ì‚¬ì´ë©´ í‡´ê·¼ì‹œê°„ìœ¼ë¡œ íŒë‹¨
      const secondTime = allTimes[1];
      if (secondTime.hours >= 12 && secondTime.hours <= 24) {
        endTime = parseKoreanTime(secondTime.text);
      } else {
        // 12ì‹œ ë¯¸ë§Œì´ì–´ë„ ì²« ë²ˆì§¸ë³´ë‹¤ í¬ë©´ í‡´ê·¼ìœ¼ë¡œ ê°„ì£¼
        endTime = parseKoreanTime(secondTime.text);
      }
    }

    // ì œì™¸ì‹œê°„ (ì„¸ ë²ˆì§¸ ë˜ëŠ” ê·¸ ì´í›„ì˜ ì‘ì€ ì‹œê°„)
    for (let i = 2; i < allTimes.length; i++) {
      const time = allTimes[i];
      // 0~3ì‹œ ë²”ìœ„ì˜ ì‹œê°„ì€ ì œì™¸ì‹œê°„ìœ¼ë¡œ íŒë‹¨
      if (time.hours <= 3) {
        excludeTime = time.hours * 60 + time.minutes;
        break;
      }
    }

    // ë©”ëª¨ ì¶”ì¶œ
    const memo = detectMemoKeyword(line);

    const year = inferYear(dateInfo.month, currentMonth);
    const dateStr = toISODateString(year, dateInfo.month, dateInfo.day);

    records.push({
      date: dateStr,
      startTime,
      endTime,
      lunchTime: 90,
      excludeTime,
      memo,
      confidence: calculateConfidence(startTime, endTime),
      rawText: line
    });
  }

  return records;
};
